
import Vue from "vue";
import VueRouter from "vue-router";
import Lander from "./components/Lander";
import Portfolio from "./components/Portfolio";

Vue.use(VueRouter);

const routes = [
  { path: "/", component: Lander },
  { path: "/Portfolio", component: Portfolio }
];

export default new VueRouter({
  mode: "history",
  routes
});